import { configureStore, createSlice } from "@reduxjs/toolkit";

const posts = createSlice({
  name: "posts",
  initialState: [],
  reducers: {
    setInit(state, action) {
      return action.payload; // action.payload  
    },
  },
});

export default configureStore({
  reducer: {
    posts: posts.reducer,
  },
});

export let { setInit } = posts.actions;
