import React from "react";

const Ask = () => {
  return (
    <iframe
      src={process.env.PUBLIC_URL + "/ask"}
      title="My iframe"
      style={{
        width: "30%", //  
        height: "30%", //  
        border: "none", //  
        position: "absolute",
        left: "50%", //   
        transform: "translate(-50%, 0%)", //  
      }}
    />
  );
};

export default Ask;
