import React, { useEffect, useState } from "react";
import "./Sidebar.css";
import { setInit } from "../../storage/store";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom"; // Import useNavigate
import axios from "axios";

const Sidebar = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate(); // Initialize useNavigate
  const posts = useSelector((state) => state.posts || []);

  const [isPasswordModalOpen, setIsPasswordModalOpen] = useState(false);
  const [isFormModalOpen, setIsFormModalOpen] = useState(false);
  const [password, setPassword] = useState("");
  const [formData, setFormData] = useState({
    title: "",
    url: "",
    tag: "",
  });

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(
          `${process.env.REACT_APP_API_HOST}/posts`
        );

        dispatch(setInit(response.data));
      } catch (error) {
        console.error("Failed to fetch posts:", error);
      }
    };

    fetchData();
  }, [dispatch]);

  //  
  const handlePasswordSubmit = () => {
    if (password === "admin123") {
      setIsPasswordModalOpen(false);
      setIsFormModalOpen(true);
    } else {
      alert(" .");
    }
  };

  //  
  const handleFormSubmit = async (e) => {
    e.preventDefault();

    // URL   
    const existingPost = posts.find((post) => post.url === formData.url);

    try {
      if (existingPost) {
        // PUT :   
        const response = await axios.put(
          `${process.env.REACT_APP_API_HOST}/posts/${existingPost.id}`,
          formData
        );
        console.log("Post Updated:", response.data);
        alert("  !");
      } else {
        // POST :   
        const response = await axios.post(
          `${process.env.REACT_APP_API_HOST}/posts`,
          formData
        );
        console.log("Post Created:", response.data);
        alert("  !");
      }

      setIsFormModalOpen(false);
    } catch (error) {
      console.error("Failed to submit post:", error);
      alert("  .");
    }
  };

  // Navigate to post URL
  const handleNavigate = (url) => {
    if (url == "home") {
      navigate("/");
    } else {
      navigate(`/page/${url}`);
    }
  };

  return (
    <aside className="sidebar">
      <h2></h2>
      <ul>
        <li onClick={() => handleNavigate("home")}></li>
      </ul>
      <ul>
        {posts &&
          posts.map((post) => (
            <li key={post.id} onClick={() => handleNavigate(post.url)}>
              {post.title}
            </li>
          ))}
      </ul>
      <ul>
        <button onClick={() => setIsPasswordModalOpen(true)}>
          ADMIN SETTING
        </button>
      </ul>

      {/*   */}
      {isPasswordModalOpen && (
        <div className="modal">
          <h3> </h3>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <button onClick={handlePasswordSubmit}></button>
          <button onClick={() => setIsPasswordModalOpen(false)}></button>
        </div>
      )}

      {/*   */}
      {isFormModalOpen && (
        <div className="modal">
          <h3> </h3>
          <form onSubmit={handleFormSubmit}>
            <div>
              <label>Title:</label>
              <input
                type="text"
                value={formData.title}
                onChange={(e) =>
                  setFormData({ ...formData, title: e.target.value })
                }
                required
              />
            </div>
            <div>
              <label>URL:</label>
              <input
                type="text"
                value={formData.url}
                onChange={(e) =>
                  setFormData({ ...formData, url: e.target.value })
                }
                required
              />
            </div>
            <div>
              <label>Tag:</label>
              <input
                type="text"
                value={formData.tag}
                onChange={(e) =>
                  setFormData({ ...formData, tag: e.target.value })
                }
              />
            </div>
            <button type="submit"></button>
            <button type="button" onClick={() => setIsFormModalOpen(false)}>
              
            </button>
          </form>
        </div>
      )}
    </aside>
  );
};

export default Sidebar;
